// src/components/ReferralTracker.js
import { collectionKeys } from "@core/helper/utils";
import { useEffect } from "react";
// Use this everywhere
const { getFirestore, doc, getDocs, increment, query, setDoc, updateDoc, where, collection } = require("firebase/firestore");


const ReferralTracker = ({ firestoreConfig }) => {
  const db = firestoreConfig;
  // console.log(db)  
  useEffect(() => {
    if (typeof window !== "undefined") {
      const searchParams = new URLSearchParams(window.location.search);
      const referealId = searchParams.get("ref") || "";
      const utm_source = searchParams.get("utm_source") || "";
      if (referealId) {
        const q = query(
          collection(db, collectionKeys.REFERAL),
          where("referealId", "==", referealId)
        );
        // console.log(q)

        const handleReferral = async () => {
          const querySnapshot = await getDocs(q);
          if (!querySnapshot.empty) {
            const matchingDoc = querySnapshot.docs.find(
              (doc) => doc.data().utm_source === utm_source 
            );
            if (matchingDoc) {
              const data = await updateDoc(matchingDoc.ref, {
                views: increment(1),
              });
            // }
            } else {
              const newDocRef = doc(collection(db, collectionKeys.REFERAL));
              await setDoc(newDocRef, {
                utm_source,
                referealId,
                views: 1,
                createdAt: new Date(),
              });
            }
          }
        };

        handleReferral();
      }
    }
  }, [db]);

  return null;
};

export default ReferralTracker;
